import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Avatar from "@material-ui/core/Avatar";
import Grid from "@material-ui/core/Grid";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Toolbar from "./Toolbar";
import Footer from "./Footer";
import HoverHighlight from "./HoverHighlight";
import StoryCard from "./StoryCard";
import { pageWidth } from "./PageStyles";

const StyledTabs = withStyles({
  root: {
    borderBottom: "1px solid #e8e8e8",
  },
  indicator: {
    backgroundColor: "#1890ff",
  },
})(Tabs);

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: "2vh",
    marginRight: theme.spacing(4),
    "&:hover": {
      color: "#40a9ff",
      opacity: 1,
    },
    "&$selected": {
      color: "#1890ff",
      fontWeight: theme.typography.fontWeightMedium,
    },
    "&:focus": {
      color: "#40a9ff",
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    width: `${pageWidth}%`,
    textAlign: "center",
    margin: "0 auto",
    marginTop: "8vh",
  },
  username: {
    fontSize: "4vh",
    marginTop: "1vh",
  },
  realname: {
    fontSize: "2vh",
    marginTop: "2vh",
  },
  avatar: {
    width: "128px",
    height: "128px",
    fontSize: "4vh",
  },
  avatarContainer: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2vh",
  },
  stories: {
    width: "92%",
    textAlign: "center",
    margin: "0 auto",
    marginLeft: "32px",
  },
  tabs: {
    width: "90%",
    textAlign: "center",
    margin: "0 auto",
  },
}));

export default function Profile() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const stories = () => {
    let content = [];
    for (var i = 0; i < 10; i++) {
      content.push(
        <Grid item xs={6}>
          <StoryCard value={value} />
        </Grid>
      );
    }
    return content;
  };

  return (
    <div>
      <Toolbar />
      <div className={classes.root}>
        <Grid container spacing={3} className={classes.menu}>
          <Grid item xs={3}>
            <div className={classes.avatarContainer}>
              <Avatar className={classes.avatar}>UM</Avatar>
            </div>
            <div className={classes.username}>
              <HoverHighlight>Username McUserface</HoverHighlight>
            </div>
            <div className={classes.realname}>Realname Lastname</div>
            <Button variant="outlined" className={classes.button}>
              Edit Profile
            </Button>
          </Grid>

          <Grid item xs={9}>
            <StyledTabs
              value={value}
              onChange={handleChange}
              className={classes.tabs}
            >
              <StyledTab label="Stories" />
              <StyledTab label="Shorts" />
              <StyledTab label="Characters" />
              <StyledTab label="Remixes" />
            </StyledTabs>
            <Grid container spacing={2} className={classes.stories}>
              {stories()}
            </Grid>
          </Grid>
        </Grid>
      </div>

      <Footer />
    </div>
  );
}
